import { removeToken } from '@/utils/auth'
export default {
  name: 'app',
  data () {
    return {
      num: 0
    }
  },
  methods: {
    showLog () {
      this.num++
      const dom = document.querySelector('#__vconsole')
      if (dom !== null && this.num > 4) {
        dom.style.display = 'block'
        this.num = 0
      }
    }
  },
  mounted () {
    this.$store.dispatch('GetData')
  },
  beforeDestroy () {
    removeToken()
  }
}



