import request from '@/utils/request'

export function listAllProcessApply (data) {
  // 用章---待审核列表
  return request({
    url: '/official/listMobileMyTodoApplication',
    method: 'post',
    data
  })
}

export function Processparticipate (data) {
  // 用章---参与过列表
  return request({
    url: '/official/listMobileMyInvolvedApplication',
    method: 'post',
    data
  })
}

export function listGoingProcessApply (data) {
  // 用章---申请中列表
  return request({
    url: '/official/listMobileMyApplication',
    method: 'post',
    data
  })
}

export function completeAllList (data) {
  //  用章---已完成列表
  return request({
    url: '/official/listMobileMyDoneApplication',
    method: 'post',
    data
  })
}
export function ApplicationDetial (data) {
  //  用章---详细信息
  return request({
    url: '/official/selectApplication',
    method: 'get',
    params: data
  })
}
export function startApplication (data) {
  //  用章---申请表单
  return request({
    url: '/official/startApplication',
    method: 'post',
    data
  })
}

export function CancelApplication (data) {
  //  用章---取消申请
  return request({
    url: '/official/cancelApplication',
    method: 'get',
    params: data
  })
}

export function HandleApplication (data) {
  //  用章---办理申请
  return request({
    url: '/official/completeApplication',
    method: 'post',
    data
  })
}

export function claimApplication (data) {
  //  用章---签收
  return request({
    url: '/application/claimTask',
    method: 'post',
    data
  })
}

export function CompanyList (data) {
  // 公司列表
  return request({
    url: '/official/listLoanCompany',
    method: 'get'
  })
}

export function OfficialsealList (data) {
  // 公章列表
  return request({
    url: '/official/listLoanItem',
    method: 'get',
    params: data
  })
}
// 陪同人接口
export function searchDepartment (data) {
  return request({
    url: '/employee/listDepartmentLeaderOptions',
    method: 'get',
    params: data
  })
}

export function uploadAttachment (data) {
  // 上传
  return request({
    url: 'http://oaimg.mytcloud.com/api/application/uploadAttachment',
    method: 'post',
    data
  })
}



