
import { CompanyList } from '@/api/index'
const app = {
  state: {
    ComDataList: {}
  },
  mutations: {
    SET_COMPANY: (state, value) => {
      state.ComDataList = value
    }
  },
  actions: {
    GetData ({ commit }) {
      return new Promise((resolve, reject) => {
        CompanyList().then(response => {
          commit('SET_COMPANY', response.result)
          resolve(response.result)
        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}
export default app



