import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    // {
    //   path: '/main',
    //   name: 'main',
    //   meta: { title: '我的汇总' },
    //   component: () => import('@/views/home/index')
    // },
    {
      path: '/home',
      name: 'home',
      meta: { title: '我的申请' },
      component: () => import('@/views/leave/index')
    },
    {
      path: '/applyLeave',
      name: 'applyLeave',
      meta: { title: '盖章申请' },
      component: () => import('@/views/leave/applyLeave')
    },
    {
      path: '/overTimeLeave',
      name: 'overTimeLeave',
      meta: { title: '借章申请' },
      component: () => import('@/views/leave/overTimeLeave')
    },
    {
      path: '/reportLeave',
      name: 'reportLeave',
      meta: { title: '执照副本申请' },
      component: () => import('@/views/leave/reportLeave')
    },
    {
      path: '/abnormal',
      name: 'abnormal',
      meta: { title: '执照原件申请' },
      component: () => import('@/views/leave/abnormal')
    },
    {
      path: '/leaveDetail',
      name: 'leaveDetail',
      meta: { title: '假期详情' },
      component: () => import('@/views/leave/leaveDetail')
    }
  ]
})



